const IS_SUBMITED = ref(false)
//  Reset Form
const setFormData = (data) => {
  resetForm({
    values: {
      is_google_login: data.is_google_login,
      // is_facebook_login: data.is_facebook_login,
      is_webpush_notification: data.is_webpush_notification,
      is_mail_notification: data.is_mail_notification,
      is_mobile_notification: data.is_mobile_notification,
      is_custom_webhook_notification: data.is_custom_webhook_notification,
      google_secretkey: data.google_secretkey,
      google_publickey: data.google_publickey,
      // facebook_secretkey: data.facebook_secretkey,
      // facebook_publickey: data.facebook_publickey,
      onesignal_app_id: data.onesignal_app_id,
      onesignal_rest_api_key: data.onesignal_rest_api_key,
      onesignal_channel_id: data.onesignal_channel_id,
      is_map_key: data.is_map_key,
      google_maps_key: data.google_maps_key,
      is_application_link: data.is_application_link,
      customer_app_play_store: data.customer_app_play_store,
      customer_app_app_store: data.customer_app_app_store,

    }
  })
}

//validation
// const validationSchema = yup.object({
//   google_secretkey: yup.string({ required_error: "Google key is required", }).url("Must be a valid Google key"),
//   google_publickey: yup.string({ required_error: "Google Publickey is required", }).url("Must be a valid Google Publickey"),
//   facebook_secretkey: yup.string({ required_error: "Facebook Publickey is required", }).url("Must be a valid Facebook Publickey"),
//   facebook_publickey: yup.string({ required_error: "Facebook Publickey is required", }).url("Must be a valid Facebook Publickey"),
//   onesignal_app_id: yup.string({ required_error: "APP ID is required", }).url("Must be a valid APP ID"),
