const { value: customer_app_play_store } = useField('customer_app_play_store')
const { value: customer_app_app_store } = useField('customer_app_app_store')



// message
const display_submit_message = (res) => {
  IS_SUBMITED.value = false;
  if (res.status) {
    window.successSnackbar(res.message)
  } else {
    window.errorSnackbar(res.message)
    errorMessages.value = res.errors
  }
}

//fetch data
const data = 'is_google_login,is_webpush_notification,is_mail_notification,is_mobile_notification,is_map_key,is_application_link,is_custom_webhook_notification,google_secretkey,google_publickey,onesignal_app_id,onesignal_rest_api_key,onesignal_channel_id,google_maps_key,customer_app_play_store,customer_app_app_store'
onMounted(() => {
  createRequest(GET_URL(data)).then((response) => {
    setFormData(response)
  })
})

//Form Submit
const formSubmit = handleSubmit((values) => {
  IS_SUBMITED.value = true;
  const newValues = {}
  Object.keys(values).forEach((key) => {
    newValues[key] = values[key] || ''
  })
  storeRequest({
    url: STORE_URL,
    body: newValues
  }).then((res) => display_submit_message(res))
})

defineProps({
  label: { type: String, default: '' },
  modelValue: { type: String, default: '' },
  placeholder: { type: String, default: '' },
  errorMessage: { type: String, default: '' },
  errorMessages: { type: Array, default: () => [] }
})

</script>
